import React from "react";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Button from "@material-ui/core/Button";
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import RostelecomLogo from "assets/svg/logo.svg";
import $ from "jquery";
import { Link } from "react-router-dom";
import IconButton from "@material-ui/core/IconButton";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import Hidden from "@material-ui/core/Hidden";

class Header extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      displaySelfRegBtn: false,
      selfregBtnLink: "",
      anchorEl: "",
      openMenu: false
    };
  }

  componentDidMount() {
    return new Promise(function(resolve, reject) {
      $.ajax({
        type: "GET",
        url:
          process.env.NODE_ENV === "production"
            ? "assets/landing/siteConfig.json"
            : "siteConfig.json",
        dataType: "json",
        success: function(data) {
          resolve(data);
        },
        error: function(error) {
          reject(error);
        }
      });
    })
      .then(response => {
        if (response.hasOwnProperty("selfRegButton")) {
          this.setState({
            displaySelfRegBtn: response.selfRegButton.display,
            selfregBtnLink: response.selfRegButton.href
          });
        }
      })
      .catch(reason => {
        console.warn(reason);
      });
  }

  openMenu(e) {
    this.setState({
      openMenu: true,
      anchorEl: e.currentTarget
    });
  }

  closeMenu() {
    this.setState({
      openMenu: false
    });
  }

  reloadPage() {
    setTimeout(() => {
      document.location.reload();
    }, 0);
  }

  render() {
    return (
      <AppBar position={"relative"} style={{ backgroundColor: "transparent" }}>
        <Toolbar>
          <Container maxWidth={"lg"} style={{ height: "100%" }}>
            <Hidden mdUp>
              <Grid container item sm={12} xs={12} justify={"space-between"}>
                <Grid item container sm={2} xs={2} alignItems={"center"}>
                  <Link to={"/"}>
                    <img
                      src={RostelecomLogo}
                      alt={" "}
                      style={{ height: "45px" }}
                    />
                  </Link>
                </Grid>
                <Grid item container sm={1} xs={1}>
                  <IconButton
                    aria-label="more"
                    aria-controls="long-menu"
                    aria-haspopup="true"
                    onClick={this.openMenu.bind(this)}
                  >
                    <MoreVertIcon />
                  </IconButton>
                  <Menu
                    id="long-menu"
                    anchorEl={this.state.anchorEl}
                    keepMounted
                    open={this.state.openMenu}
                    onClose={this.closeMenu.bind(this)}
                  >
                    <MenuItem onClick={this.closeMenu.bind(this)}>
                      <Link
                        to="/Functional"
                        style={{
                          textDecoration: "none"
                        }}
                      >
                        <Button
                          style={{
                            textTransform: "none",
                            fontFamily: "RostelecomBasisMedium",
                            fontSize: "1rem"
                          }}
                        >
                          
                        </Button>
                      </Link>
                    </MenuItem>
                    <MenuItem onClick={this.closeMenu.bind(this)}>
                      <Link
                        to="/Integrations"
                        style={{
                          textDecoration: "none"
                        }}
                      >
                        <Button
                          style={{
                            textTransform: "none",
                            fontFamily: "RostelecomBasisMedium",
                            fontSize: "1rem"
                          }}
                        >
                          
                        </Button>
                      </Link>
                    </MenuItem>
                    <MenuItem onClick={this.closeMenu.bind(this)}>
                      <Link
                        to="/Tariffs"
                        style={{
                          textDecoration: "none"
                        }}
                      >
                        <Button
                          style={{
                            textTransform: "none",
                            fontFamily: "RostelecomBasisMedium",
                            fontSize: "1rem"
                          }}
                        >
                          
                        </Button>
                      </Link>
                    </MenuItem>
                    <MenuItem onClick={this.closeMenu.bind(this)}>
                      <Link
                        to="/FAQ"
                        style={{
                          textDecoration: "none"
                        }}
                      >
                        <Button
                          style={{
                            textTransform: "none",
                            fontFamily: "RostelecomBasisMedium",
                            fontSize: "1rem"
                          }}
                        >
                          FAQ
                        </Button>
                      </Link>
                    </MenuItem>
                    <MenuItem onClick={this.closeMenu.bind(this)}>
                      <Button
                        href="/user/login"
                        variant="contained"
                        color="primary"
                        style={{
                          textTransform: "none",
                          fontFamily: "RostelecomBasisRegular",
                          fontSize: "1rem",
                          width: "100%"
                        }}
                      >
                        
                      </Button>
                    </MenuItem>
                    <MenuItem onClick={this.closeMenu.bind(this)}>
                      <Button
                        variant="outlined"
                        color="default"
                        href={this.state.selfregBtnLink}
                        style={{
                          display: this.state.displaySelfRegBtn
                            ? "block"
                            : "none",
                          textTransform: "none",
                          fontFamily: "RostelecomBasisRegular",
                          color: "#909090",
                          fontSize: "1rem"
                        }}
                      >
                        
                      </Button>
                    </MenuItem>
                  </Menu>
                </Grid>
              </Grid>
            </Hidden>
            <Hidden smDown>
              <Grid
                container
                item
                justify={"space-between"}
                style={{ height: "100%" }}
              >
                <Grid item container md={1} alignItems={"center"}>
                  <Link
                    to={"/"}
                    onClick={() => {
                      this.reloadPage().bind(this);
                    }}
                  >
                    <img
                      src={RostelecomLogo}
                      alt={" "}
                      style={{ height: "45px" }}
                    />
                  </Link>
                </Grid>
                <Grid
                  item
                  container
                  md={9}
                  lg={7}
                  justify={"space-between"}
                  alignItems={"center"}
                >
                  <Link
                    to="/Functional"
                    style={{
                      textDecoration: "none"
                    }}
                  >
                    <Button
                      style={{
                        textTransform: "none",
                        fontFamily: "RostelecomBasisMedium",
                        fontSize: "1rem"
                      }}
                    >
                      
                    </Button>
                  </Link>
                  <Link
                    to="/Integrations"
                    style={{
                      textDecoration: "none"
                    }}
                  >
                    <Button
                      style={{
                        textTransform: "none",
                        fontFamily: "RostelecomBasisMedium",
                        fontSize: "1rem"
                      }}
                    >
                      
                    </Button>
                  </Link>
                  <Link
                    to="/Tariffs"
                    style={{
                      textDecoration: "none"
                    }}
                  >
                    <Button
                      style={{
                        textTransform: "none",
                        fontFamily: "RostelecomBasisMedium",
                        fontSize: "1rem"
                      }}
                    >
                      
                    </Button>
                  </Link>
                  <Link
                    to="/FAQ"
                    style={{
                      textDecoration: "none"
                    }}
                  >
                    <Button
                      style={{
                        textTransform: "none",
                        fontFamily: "RostelecomBasisMedium",
                        fontSize: "1rem"
                      }}
                    >
                      FAQ
                    </Button>
                  </Link>
                  <Button
                    href="/user/login"
                    variant="contained"
                    color="primary"
                    style={{
                      textTransform: "none",
                      fontFamily: "RostelecomBasisRegular",
                      fontSize: "1rem"
                    }}
                  >
                    
                  </Button>
                  <Button
                    variant="outlined"
                    color="default"
                    href={this.state.selfregBtnLink}
                    style={{
                      display: this.state.displaySelfRegBtn ? "block" : "none",
                      textTransform: "none",
                      fontFamily: "RostelecomBasisRegular",
                      color: "#909090",
                      fontSize: "1rem"
                    }}
                  >
                    
                  </Button>
                </Grid>
              </Grid>
            </Hidden>
          </Container>
        </Toolbar>
      </AppBar>
    );
  }
}
export default Header;
