/*eslint-disable*/
import React from "react";
// react components for routing our app without refresh
import { Link } from "react-router-dom";

// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";

// core components
import CustomDropdown from "components/CustomDropdown/CustomDropdown.jsx";
import Button from "components/CustomButtons/Button.jsx";

import headerLinksStyle from "assets/jss/material-kit-react/components/headerLinksStyle.jsx";
const headerLinksTop = {
  fontSize: '1rem'
};
const addStlTpBtn = {
  marginLeft: '60px',
  float: 'left',
  color: 'inherit',
  width: 'auto',
  display: 'block',
  padding: '0',
  position: 'relative'
};
function HeaderLinks({ ...props }) {
  const { classes } = props;
  return (
    <List className={classes.list}>
      <ListItem className={classes.listItem}>
        <Link
          to="/Functional"
          style={headerLinksTop}
          color="transparent"
          className={classes.navLink}
        >
          
        </Link>
      </ListItem>
      <ListItem className={classes.listItem}>
        <Link
          to="/Integrations"
          style={headerLinksTop}
          color="transparent"
          className={classes.navLink}
        >
          
        </Link>
      </ListItem>
      <ListItem className={classes.listItem}>
        <Link
          to="/Tariffs"
          color="transparent"
          className={classes.navLink}
          style={headerLinksTop}
        >
          
        </Link>
      </ListItem>
      <ListItem className={classes.listItem}>
        <CustomDropdown
          noLiPadding
          buttonText="FAQ"
          buttonProps={{
            className: classes.navLink,
            color: "transparent"
          }}
          dropdownList={[
            <Link to="/Documentations" className={classes.dropdownLink}>
             
            </Link>,
            <Link to="/FAQ" className={classes.dropdownLink}>
              
            </Link>,
            <Link to="/Questions" className={classes.dropdownLink}>
              -
            </Link>
          ]}
        />
      </ListItem>
      <ListItem style={addStlTpBtn}>
        <Button color="rose" href="/user/login"></Button>
      </ListItem>
    </List>
  );
}

export default withStyles(headerLinksStyle)(HeaderLinks);
