import React from "react";
import Container from "@material-ui/core/Container/Container";
import Grid from "@material-ui/core/Grid/Grid";
import Typography from "@material-ui/core/Typography/Typography";
import TextField from "@material-ui/core/TextField/TextField";
import Button from "@material-ui/core/Button/Button";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles(() => ({
  feedbackFormContainer: {
    "@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none)": {
      height: "430pt",
      minHeight: "100%"
    },
    height: "auto"
  }
}));

function FeedbackForm() {
  const classes = useStyles();

  return (
    <Container
      maxWidth={false}
      style={{ paddingBottom: "50pt" }}
      className={classes.feedbackFormContainer}
    >
      <Grid
        container
        direction={"column"}
        justify={"space-around"}
        alignItems={"center"}
        style={{ height: "100%" }}
      >
        <a name="feedback">
          <Typography
            variant={"h4"}
            color={"textPrimary"}
            style={{
              fontFamily: "RostelecomBasisMedium",
              paddingTop: "40pt",
              fontSize: "1.7rem"
            }}
          >
             
          </Typography>
        </a>
        <Typography
          variant={"h6"}
          color={"textPrimary"}
          style={{ fontSize: "1.1rem", textAlign: "center" }}
        >
            ,    
        </Typography>
        <Grid container item justify={"space-between"} md={7} spacing={3}>
          <Grid item md={6} sm={6} xs={12}>
            <TextField label={" "} fullWidth={true} />
          </Grid>
          <Grid item md={6} sm={6} xs={12}>
            <TextField label={" email"} fullWidth={true} />
          </Grid>
          <Grid item md={12} sm={12} xs={12}>
            <TextField
              label={" "}
              fullWidth={true}
              multiline={true}
              rows="6"
            />
          </Grid>
        </Grid>
        <Button
          variant={"outlined"}
          style={{
            backgroundColor: "transparent",
            height: "64px",
            textTransform: "none",
            fontSize: "1.2rem",
            fontFamily: "RostelecomBasisMedium",
            width: "200px",
            marginTop: "20pt"
          }}
          color={"primary"}
        >
          
        </Button>
      </Grid>
    </Container>
  );
}

export default FeedbackForm;
