import Components from "../views/Components/Components.jsx";
import LandingPage from "../views/LandingPage/LandingPage.jsx";
import Functional from "../views/Functional/Functional.jsx";
import Integrations from "../views/Integrations/Integrations.jsx";
import Tariffs from "../views/Tarrifs/Tariffs.jsx";
import Documentations from "../views/Documentation/Documentations.jsx";
import Questions from "../views/Questions/Questions.jsx";
import Faq from "../views/FAQ/FAQ";

var indexRoutes = [
  { path: "/Components", name: "Components", component: Components },
  { path: "/Functional", name: "Functional", component: Functional },
  { path: "/Integrations", name: "Integrations", component: Integrations },
  { path: "/Tariffs", name: "Tariffs", component: Tariffs },
  {
    path: "/Documentations",
    name: "Documentations",
    component: Documentations
  },
  { path: "/FAQ", name: "FAQ", component: Faq },
  { path: "/Questions", name: "Questions", component: Questions },
  { path: "/", name: "LandingPage", component: LandingPage }
];

export default indexRoutes;
