import React from "react";
import Header from "components/Header/Header.jsx";
import Footer from "components/Footer/Footer.jsx";
import HeaderLinks from "components/Header/HeaderLinks.jsx";
import Container from "@material-ui/core/Container/Container";
import { makeStyles } from "@material-ui/core";
import FeedbackForm from "../../components/FeedbackForm";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography/Typography";

const useStyles = makeStyles(theme => ({
  featuresMainContainer: {
    padding: "0!important"
  },
  featuresCommonContainer: {
    backgroundColor: "#e3e8ec",
    paddingBottom: "40pt"
  },
  featuresMainDescr: {
    fontFamily: "RostelecomBasisMedium!important",
    marginBottom: "10pt!important"
  },
  featuresAdditionalDescr: {
    fontSize: "1rem!important"
  },
  featuresTitle: {
    marginTop: "0pt!important",
    paddingTop: "50pt",
    paddingBottom: "14pt"
  },
  integrationDescrContainer: {
    backgroundColor: "#ffffff",
    padding: "40pt",
    marginBottom: "20pt",
    borderRadius: "0.4%"
  },
  [theme.breakpoints.down("xs")]: {
    integrationDescrContainer: {
      padding: "0",
      backgroundColor: "#e3e8ec"
    }
  }
}));

function Integrations() {
  const classes = useStyles();

  return (
    <Container maxWidth={false} className={classes.featuresMainContainer}>
      <Header rightLinks={<HeaderLinks />} fixed />
      <Container maxWidth={false} className={classes.featuresCommonContainer}>
        <Container maxWidth={"lg"}>
          <h1 className={classes.featuresTitle}></h1>
          <Grid
            container
            item
            md={12}
            justify={"flex-start"}
            alignItems={"center"}
            className={classes.integrationDescrContainer}
          >
            <Typography
              variant={"h6"}
              color={"textPrimary"}
              className={classes.featuresMainDescr}
            >
                1
            </Typography>
            <Typography
              variant={"subtitle2"}
              color={"textPrimary"}
              className={classes.featuresAdditionalDescr}
            >
                  , 
                  
               1,     -, ,
              ,      1   
               ,     
              .
            </Typography>
            <Typography
              variant={"subtitle2"}
              color={"textPrimary"}
              className={classes.featuresAdditionalDescr}
            >
                   
               -, ,    ,
                  24  2016 . N -7-15/155@ 
                    
              .
            </Typography>
            <Typography
              variant={"subtitle2"}
              color={"textPrimary"}
              className={classes.featuresAdditionalDescr}
            >
                     1.
                      
               1.  1    
              .
            </Typography>
          </Grid>
          <Grid
            container
            item
            md={12}
            justify={"flex-start"}
            alignItems={"center"}
            className={classes.integrationDescrContainer}
            id={"SapOracleContainer"}
          >
            <Typography
              variant={"h6"}
              color={"textPrimary"}
              className={classes.featuresMainDescr}
            >
                Oracle  SAP
            </Typography>
            <Typography
              variant={"subtitle2"}
              color={"textPrimary"}
              className={classes.featuresAdditionalDescr}
            >
               ,   ERP-, 
                      
                 .   
                   ERP   
               ,      
                 ERP .
            </Typography>
          </Grid>
        </Container>
      </Container>
      <FeedbackForm />
      <Footer />
    </Container>
  );
}

export default Integrations;
