import React from "react";
import Line from "../components/Line";
import Container from "@material-ui/core/Container/Container";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core";
import { StylesProvider, createGenerateClassName } from "@material-ui/styles";
import Tab from "@material-ui/core/Tab/Tab";
import Tabs from "@material-ui/core/Tabs/Tabs";
import Grid from "@material-ui/core/Grid/Grid";

const generateClassName = createGenerateClassName({
  productionPrefix: "prod"
});

const mapConfig = {
  constraints: {
    allowYearly: false,
    allowManualEdit: true,
    disallowEdit: ["countLine"],
    thatDefaultTarif: { count: 1000, value: 6400 }
  },
  map: {
    montly: {
      type: "plainMap",
      plainMap: [
        { count: 100, value: 88 },
        { count: 200, value: 170 },
        { count: 300, value: 240 },
        { count: 400, value: 300 },
        { count: 500, value: 350 },
        { count: 600, value: 408 },
        { count: 700, value: 469 },
        { count: 800, value: 528 },
        { count: 900, value: 585 },
        { count: 1000, value: 640 },
        { count: 2000, value: 1240 },
        { count: 3000, value: 1800 },
        { count: 4000, value: 2360 },
        { count: 5000, value: 2900 },
        { count: 6000, value: 3420 },
        { count: 7000, value: 3920 },
        { count: 8000, value: 4400 },
        { count: 9000, value: 4860 },
        { count: 10000, value: 5300 }
      ],
      price: { limit: [0, 15], dot: 0.1 }
    },
    yearly: {
      type: "sale",
      sale: [
        { count: 100, value: 880 },
        { count: 200, value: 1700 },
        { count: 300, value: 2400 },
        { count: 400, value: 3000 },
        { count: 500, value: 3500 },
        { count: 600, value: 4080 },
        { count: 700, value: 4690 },
        { count: 800, value: 5280 },
        { count: 900, value: 5850 },
        { count: 1000, value: 6400 },
        { count: 2000, value: 12400 },
        { count: 3000, value: 18000 },
        { count: 4000, value: 23600 },
        { count: 5000, value: 29000 },
        { count: 6000, value: 34200 },
        { count: 7000, value: 39200 },
        { count: 8000, value: 44000 },
        { count: 9000, value: 48600 },
        { count: 10000, value: 53000 }
      ],
      price: { limit: [0, 15], dot: 0.1 }
    }
  }
};

const styles = theme => ({
  calculatorContainer: {
    padding: 0,
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-between",
    height: "330pt",
    paddingTop: "20pt"
  },
  featuresTitle: {
    paddingBottom: "0",
    fontFamily: "RostelecomBasisBold!important",
    fontSize: "2rem"
  },
  btnSelectTarif: {
    fontFamily: "RostelecomBasisLight!important",
    fontSize: "1.9rem",
    textTransform: "none"
  },
  [theme.breakpoints.down("xs")]: {
    btnSelectTarif: {
      fontSize: "0.7em"
    },
    featuresTitle: {
      fontSize: "1.5em"
    }
  }
});

const StyledTabs = withStyles({
  indicator: {
    "& > div": {
      backgroundColor: "#ff4f12"
    }
  },
  flexContainer: {
    borderBottom: "2px solid #d2d5d8"
  }
})(props => <Tabs {...props} TabIndicatorProps={{ children: <div /> }} />);

const StyledTab = withStyles(theme => ({
  root: {
    textTransform: "none",
    fontFamily: "RostelecomBasisBold",
    fontSize: theme.typography.pxToRem(16)
  }
}))(props => <Tab {...props} />);

class Calculator extends React.Component {
  constructor(props) {
    super(props);
    let map = mapConfig,
      refObj = {
        count: React.createRef(),
        price: React.createRef(),
        pay: React.createRef()
      },
      constraints = map.constraints,
      montlyView = false,
      defaultTarif = constraints.thatDefaultTarif,
      maps = map.map,
      currentViewMap = this.selectMontlyOrYearlyCtx(montlyView, maps),
      lines = this.createLines(currentViewMap, refObj);

    this.state = {
      lines: lines,
      constraints: constraints,
      maps: maps,
      currentViewMap: currentViewMap,
      defaultTarif: defaultTarif,
      tabValue: 0,
      montlyView: montlyView,
      refObj: refObj,
      currentValueLines: {},
      onValueChanged: props.onValueChanged
    };
  }

  makeGeneratorForLine(generatorType, generatorCtx) {
    switch (generatorType) {
      case Calculator.Enums.GeneratorType.Continuous:
        let from = generatorCtx.limit[0],
          to = generatorCtx.limit[1],
          step = generatorCtx.dot || 0.1,
          valuesGenerator = function(onLine) {
            let items = [];
            while (from !== to) {
              from = (from + step).toFixed(1);
              items.push({
                elem: onLine + from,
                value: from
              });
              from = parseFloat(from);
            }
            return items;
          };
        return function(lineName) {
          return valuesGenerator(lineName);
        };

      case Calculator.Enums.GeneratorType.PlainMap:
        return function(lineName) {
          return generatorCtx.map(function(currentVal) {
            return {
              elem: lineName + currentVal,
              value: currentVal
            };
          });
        };

      case Calculator.Enums.GeneratorType.Sale:
        return function(lineName) {
          return generatorCtx.map(function(currentVal) {
            return {
              elem: lineName + currentVal,
              value: currentVal
            };
          });
        };

      default:
        throw new Error("Wrong generator type");
    }
  }

  selectMontlyOrYearlyCtx(montlyPlease, map) {
    return montlyPlease ? map.montly : map.yearly;
  }

  valueHasChanged(lineName, value) {
    let that = this,
      changedRules = {
        count: function(value) {
          that.selectTarif(that.selectTarifByCount(value));
        },
        pay: function(value) {
          that.selectTarif(that.selectTarifByPay(value));
        },
        price: function() {}
      };
    changedRules[lineName](value);
  }

  createLines(currentViewMap, refObj) {
    let convertToValues = function(tarifLine, selectCountOrValue) {
        return selectCountOrValue ? tarifLine.count : tarifLine.value;
      },
      valuesForCount = function(tarifLine) {
        return convertToValues(tarifLine, true);
      },
      valuesForPay = function(tarifLine) {
        return convertToValues(tarifLine, false);
      },
      tarifs = currentViewMap[currentViewMap.type],
      priceProp = currentViewMap.price,
      countProp = tarifs.map(valuesForCount),
      payProp = tarifs.map(valuesForPay);

    let that = this,
      createLine = function(lineProps, generatorForLine) {
        return (
          <Line
            name={lineProps.name}
            bottomDescription={lineProps.bottomDescription}
            adornmentElements={lineProps.adornmentElements}
            generator={generatorForLine}
            onValueChanged={that.valueHasChanged.bind(that)}
            ref={refObj[lineProps.name]}
          />
        );
      };
    return {
      count: createLine(
        Line.propsUISpecificParams.CountLine,
        this.makeGeneratorForLine(currentViewMap.type, countProp)
      ),
      price: createLine(
        Line.propsUISpecificParams.CostLine,
        this.makeGeneratorForLine(
          Calculator.Enums.GeneratorType.Continuous,
          priceProp
        )
      ),
      pay: createLine(
        Line.propsUISpecificParams.PayLine,
        this.makeGeneratorForLine(currentViewMap.type, payProp)
      )
    };
  }

  componentDidMount() {
    this.slideToPreSelectTariff(
      Calculator.Enums.LineNames.CountLine,
      this.state.defaultTarif.count
    );
  }

  slideToPreSelectTariff(toLine, toValue) {
    setTimeout(() => {
      this.state.refObj[toLine].current.slideToId(
        this.makeSlideTargetId(toLine, toValue)
      );
    }, 500);
  }

  makeSlideTargetId(toLine, toValue) {
    let slideToParams = {};
    slideToParams.line = toLine;
    slideToParams.value = toValue;
    return slideToParams;
  }

  selectDefaultTarif(index) {
    let tarif = this.state.currentViewMap[this.state.currentViewMap.type][
      index
    ];
    this.selectTarif(tarif);
  }

  selectTarif(tarif) {
    let price = this.calculatePriceByTarif(tarif); //  { "count": 10000, "value": 10 }, price = 6.1

    this.setCurrentSelectTariff(price, tarif.count, tarif.value);

    this.state.refObj[Calculator.Enums.LineNames.PriceLine].current.slideToId(
      this.makeSlideTargetId(Calculator.Enums.LineNames.PriceLine, price)
    );
    this.state.refObj[Calculator.Enums.LineNames.CountLine].current.slideToId(
      this.makeSlideTargetId(Calculator.Enums.LineNames.CountLine, tarif.count)
    );
    this.state.refObj[Calculator.Enums.LineNames.PayLine].current.slideToId(
      this.makeSlideTargetId(Calculator.Enums.LineNames.PayLine, tarif.value)
    );
  }

  onValueChangeToParentTariffs(lineCountCurrentValue) {
    this.state.onValueChanged(lineCountCurrentValue);
  }

  setCurrentSelectTariff(tarifPrice, tarifCount, tarifValue) {
    this.setState(
      {
        currentValueLines: {
          price: tarifPrice,
          count: tarifCount,
          value: tarifValue
        }
      },
      () => {
        this.onValueChangeToParentTariffs(this.state.currentValueLines.count);
      }
    );
  }

  calculatePriceByTarif(tarif) {
    if (this.state.montlyView)
      return ((tarif.value * 12) / tarif.count).toFixed(1);

    return (tarif.value / tarif.count).toFixed(1);
  }

  selectTarifByCount(count) {
    return this.state.currentViewMap[this.state.currentViewMap.type].find(
      function(tarifLine) {
        return tarifLine.count === count;
      }
    );
  }

  selectTarifByPay(pay) {
    return this.state.currentViewMap[this.state.currentViewMap.type].find(
      function(tarifLine) {
        return tarifLine.value === pay;
      }
    );
  }

  loadMap(pathToMap) {
    return new Promise(function(resolve, reject) {
      setTimeout(function() {
        resolve(mapConfig);
      }, 3000);
      // reject('Error!');
    });
  }

  handleChangePayType(event, newValue) {
    this.setState(
      {
        montlyView:
          this.state.tabValue === newValue
            ? this.state.montlyView
            : !this.state.montlyView,
        tabValue: newValue
      },
      () => {
        this.setState(
          {
            currentViewMap: this.selectMontlyOrYearlyCtx(
              this.state.montlyView,
              this.state.maps
            )
          },
          () => {
            this.passToLineMontlyViewBool(this.state.montlyView);
            this.createNewLines();
          }
        );
      }
    );
  }

  createNewLines() {
    this.setState({
      lines: this.createLines(
        this.selectMontlyOrYearlyCtx(this.state.montlyView, this.state.maps),
        this.state.refObj
      )
    });
  }

  passToLineMontlyViewBool(montlyView) {
    this.state.refObj[
      Calculator.Enums.LineNames.PriceLine
    ].current.changeInputAdornmentDescription(montlyView);
    this.state.refObj[
      Calculator.Enums.LineNames.CountLine
    ].current.changeInputAdornmentDescription(montlyView);
    this.state.refObj[
      Calculator.Enums.LineNames.PayLine
    ].current.changeInputAdornmentDescription(montlyView);
  }

  render() {
    const { classes } = this.props;

    return (
      <StylesProvider generateClassName={generateClassName}>
        <Container maxWidth={false} className={classes.calculatorContainer}>
          <Container maxWidth={"lg"}>
            <Grid
              item
              container
              md={12}
              justify={"space-between"}
              alignItems={"center"}
            >
              <h1 className={classes.featuresTitle} id={"tarifHeader"}>
                :{" "}
                <span className={classes.btnSelectTarif}>
                  {this.state.currentValueLines.count}  
                </span>
              </h1>
              <StyledTabs
                value={this.state.tabValue}
                onChange={this.handleChangePayType.bind(this)}
                aria-label="simple tabs"
                className={classes.tabsStyles}
              >
                <StyledTab label="" />
                <StyledTab label="" />
              </StyledTabs>
            </Grid>
          </Container>
          {this.state.lines.count}
          {this.state.lines.price}
          {this.state.lines.pay}
        </Container>
      </StylesProvider>
    );
  }
}

Calculator.propTypes = {
  classes: PropTypes.object.isRequired,
  fromURI: PropTypes.string.isRequired,
  onValueChanged: PropTypes.func.isRequired
};

Calculator.Enums = {
  GeneratorType: {
    Continuous: "continuous",
    PlainMap: "plainMap",
    Sale: "sale"
  },
  LineNames: {
    CountLine: "count",
    PriceLine: "price",
    PayLine: "pay"
  }
};

export default withStyles(styles)(Calculator);
