import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import CloudDownload from "@material-ui/icons/CloudDownload"
// core components
import Header from "components/Header/Header.jsx";
import Footer from "components/Footer/Footer.jsx";
import GridContainer from "components/Grid/GridContainer.jsx";
import GridItem from "components/Grid/GridItem.jsx";
import HeaderLinks from "components/Header/HeaderLinks.jsx";
import Parallax from "components/Parallax/Parallax.jsx";
import profilePageStyle from "assets/jss/material-kit-react/views/profilePage.jsx";
const offsetTop = {
  marginTop: '80px'
};
const offsetTopSmaller = {
  marginTop: '20px'
};
const DownloadIcons = {
  position: 'absolute',
  marginLeft: '11px'
};
const DownloadLinks = {
  color: '#00acc1'
};
class Documentations extends React.Component {
  render() {
    const { classes, ...rest } = this.props;
    return (
      <div>
        <Header
          rightLinks={<HeaderLinks />}
          fixed
          {...rest}
        />
        <Parallax small filter image={require("assets/img/documentation.jpg")} />
        <div className={classNames(classes.main, classes.mainRaised)}>
          <div>
            <div className={classes.container}>
              <GridContainer justify="center">
                <GridItem xs={12} sm={12} md={6} style={offsetTop}>
                  <div className={classes.profile}>
                    <div className={classes.name}>
                      <h3 className={classes.title}></h3>
                    </div>
                    <div>
                      <h6>    </h6>
                    </div>
                  </div>
                </GridItem>
              </GridContainer>
              <GridContainer justify="center" style={offsetTopSmaller}>
                <GridItem xs={12} sm={12} md={12} style={{'textAlign':'center'}}>
                  <a href={'https://edo.rt.ru/downloads/manual.rar'} download style={DownloadLinks}> <CloudDownload style={DownloadIcons}/></a>
                </GridItem>
              </GridContainer>
              <GridContainer justify="flex-start" style={offsetTop}></GridContainer>
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}

export default withStyles(profilePageStyle)(Documentations);
