import React from "react";
import PropTypes from "prop-types";
import Zoom from "@material-ui/core/Zoom";
import KeyboardArrowUp from "@material-ui/icons/KeyboardArrowUp";
import Fab from "@material-ui/core/Fab";

const absoluteButton = {
  position: "fixed",
  right: "20px",
  bottom: "30px",
  zIndex: "1000",
  width: "51px",
  height: "51px"
};

const iconSize = {
  width: "28px",
  height: "28px",
  color: "#FFFFFF"
};

class BackToTopButton extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      intervalId: 0,
      visible: false
    };
    this.hideButton = this.hideButton.bind(this);
  }
  scrollStep() {
    if (window.pageYOffset === 0) {
      clearInterval(this.state.intervalId);
    }
    window.scroll(0, window.pageYOffset - this.props.scrollStepInPx);
  }
  scrollToTop() {
    let intervalId = setInterval(
      this.scrollStep.bind(this),
      this.props.delayInMs
    );
    this.setState({ intervalId: intervalId });
  }

  componentDidMount() {
    if (this.props.scrollStepInPx) {
      window.addEventListener("scroll", this.hideButton);
    }
  }

  hideButton() {
    const { scrollStepInPx } = this.props;
    const windowsScrollTop = window.pageYOffset;
    if (windowsScrollTop > scrollStepInPx) {
      this.setState({ visible: true });
    } else {
      this.setState({ visible: false });
    }
  }

  componentWillUnmount() {
    if (this.props.scrollStepInPx) {
      window.removeEventListener("scroll", this.hideButton);
    }
  }

  render() {
    return (
      <Zoom in={this.state.visible} timeout={400}>
        <Fab
          color="primary"
          onClick={() => {
            this.scrollToTop();
          }}
          style={absoluteButton}
        >
          <KeyboardArrowUp style={iconSize} />
        </Fab>
      </Zoom>
    );
  }
}

BackToTopButton.propTypes = {
  scrollStepInPx: PropTypes.string.isRequired
};

export default BackToTopButton;
