import React from "react";
import withStyles from "@material-ui/core/styles/withStyles";
import Header from "components/Header/Header.jsx";
import Footer from "components/Footer/Footer.jsx";
import HeaderLinks from "components/Header/HeaderLinks.jsx";
import Container from "@material-ui/core/Container/Container";
import Grid from "@material-ui/core/Grid/Grid";
import Typography from "@material-ui/core/Typography/Typography";
import Box from "@material-ui/core/Box/Box";
import Button from "@material-ui/core/Button/Button";
import mainTitleImg from "../../img/svg/mainTitleImg.svg";
import iconSpeed from "../../img/svg/icon-speed.svg";
import iconSecurity from "../../img/svg/icon-security.svg";
import iconBenefit from "../../img/svg/icon-benefit.svg";
import iconGuarantee from "../../img/svg/icon-guarantee.svg";
import FeedbackForm from "../../components/FeedbackForm";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import $ from "jquery";
import "../../webGLFluid";

const dashboardRoutes = [];

const styles = theme => ({
  button: {
    margin: theme.spacing(1),
    textTransform: "none",
    fontFamily: "RostelecomBasisMedium"
  },
  buttonRegular: {
    margin: theme.spacing(1),
    textTransform: "none",
    fontFamily: "RostelecomBasisRegular"
  },
  buttonReadMore: {
    backgroundColor: "#7b939b",
    height: "64px",
    color: "#ffffff",
    textTransform: "none",
    fontSize: "1.2rem",
    fontFamily: "RostelecomBasisMedium!important",
    padding: "0 30pt 0 30pt"
  },
  input: {
    display: "none"
  },
  topMenu: {
    height: "72px"
  },
  fullHeight: {
    height: "100%"
  },
  height85: {
    height: "85%"
  },
  height65: {
    height: "65%"
  },
  bannerContainer: {
    backgroundColor: "#431625",
    height: "64px"
  },
  siteTitleContainer: {
    backgroundColor: "#313a49",
    height: "350pt"
  },
  noPadding: {
    padding: "0!important"
  },
  siteTitleTextMain: {
    fontFamily: "RostelecomBasisBold!important",
    fontSize: "2rem",
    lineHeight: "30pt"
  },
  siteTitleTextAdditional: {
    fontFamily: "RostelecomBasisMedium!important",
    fontSize: "1.3rem!important",
    lineHeight: "26pt"
  },
  siteTitleTextSecondary: {
    fontSize: "2rem",
    lineHeight: "26pt"
  },
  advantagesMainContainer: {
    paddingLeft: 0,
    paddingRight: 0,
    paddingTop: "42pt",
    paddingBottom: "42pt"
  },
  advantagesBlock: {
    textAlign: "center",
    fontFamily: "RostelecomBasisMedium!important",
    fontSize: "1.8rem!important",
    marginBottom: "42pt!important"
  },
  advantagesMainDescr: {
    fontFamily: "RostelecomBasisMedium!important",
    fontSize: "1.2rem!important",
    marginTop: "15pt!important",
    marginBottom: "15pt!important"
  },
  advantagesAdditionalDescr: {
    fontSize: "1rem!important",
    width: "80%",
    textAlign: "center"
  },
  advantagesItemContainer: {
    height: "220px"
  },
  linkToVideoBlockContainer: {
    backgroundColor: "#7b939b",
    height: "264px"
  },
  linkToVideoTextContainer: {
    height: "100%",
    paddingTop: "30pt",
    paddingBottom: "30pt",
    "& > h6": {
      zIndex: 1
    }
  },
  buttonLinkToVideo: {
    backgroundColor: "transparent",
    height: "64px",
    color: "#ffffff",
    borderColor: "#ffffff",
    textTransform: "none",
    fontSize: "1.2rem",
    width: "200pt"
  },
  detailed: {
    color: "#ffffff",
    textDecoration: "underline",
    fontFamily: "RostelecomBasisRegular!important",
    fontWeight: "normal",
    width: "71%"
  },
  triangleBottomright: {
    borderBottom: "496px solid #7b939b",
    borderLeft: "155px solid transparent",
    borderRight: "0px solid transparent",
    width: "53vw",
    position: "absolute",
    right: "0"
  },
  notebookImg: {
    width: "100%"
  },
  mainPageTitleContainer: {
    height: "65%",
    zIndex: 1
  },
  [theme.breakpoints.down("sm")]: {
    siteTitleContainer: {
      height: "auto"
    },
    mainPageTitleContainer: {
      justifyContent: "center",
      textAlign: "center",
      marginTop: "30pt",
      marginBottom: "30pt",
      "& > a": {
        textAlign: "center",
        marginTop: "25pt"
      }
    },
    mainPageUpperTitle: {
      fontSize: "0.9em"
    },
    siteTitleTextMain: {
      fontSize: "1.9em",
      lineHeight: "22pt"
    },
    siteTitleTextAdditional: {
      fontSize: "1.1em",
      lineHeight: "23pt",
      marginTop: "20pt"
    },
    buttonReadMore: {
      fontSize: "1em"
    },
    linkToVideoBlockContainer: {
      textAlign: "center"
    }
  }
});

class LandingPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      triangleBottomWidth: "",
      siteTitleContainerSize: {}
    };
  }

  componentDidMount() {
    let siteTitleElement = $("#siteTitleContainer"),
      videoContainerElement = $("#videoContainer");
    $(window).resize(() => {
      this.setTriangleBottomHeight(siteTitleElement);
      this.setCanvasSize(videoContainerElement);
    });
    this.setTriangleBottomHeightInterval(siteTitleElement);
    this.setIntervalCanvasSize(videoContainerElement);
  }

  setIntervalCanvasSize(element) {
    let timerId = setInterval(() => {
      let bannerContainerHeight = element.outerHeight(),
        bannerContainerWidth = element.outerWidth();

      this.setState({
        siteTitleContainerSize: {
          width: bannerContainerWidth,
          height: bannerContainerHeight
        }
      });
    }, 50);

    setTimeout(() => {
      clearInterval(timerId);
    }, 2000);
  }

  setTriangleBottomHeightInterval(element) {
    let timerId = setInterval(() => {
      let bannerContainerHeight = element.outerHeight();

      this.setState({
        triangleBottomWidth: bannerContainerHeight
      });
    }, 50);

    setTimeout(() => {
      clearInterval(timerId);
    }, 2000);
  }

  setTriangleBottomHeight(element) {
    let bannerContainerHeight = element.height();

    this.setState({
      triangleBottomWidth: bannerContainerHeight
    });
  }

  setCanvasSize(element) {
    let bannerContainerHeight = element.height(),
      bannerContainerWidth = element.outerWidth();

    this.setState({
      siteTitleContainerSize: {
        width: bannerContainerWidth,
        height: bannerContainerHeight
      }
    });
  }

  render() {
    const { classes, ...rest } = this.props;
    return (
      <div>
        <Header
          routes={dashboardRoutes}
          rightLinks={<HeaderLinks />}
          fixed
          {...rest}
        />
        <Container maxWidth={false} className={classes.noPadding}>
          <Container className={classes.bannerContainer} maxWidth={false}>
            <Grid
              container
              item
              justify={"center"}
              alignItems={"center"}
              className={classes.fullHeight}
            >
              <Typography
                variant={"subtitle1"}
                color={"textSecondary"}
                className={classes.mainPageUpperTitle}
              >
                       
                .{" "}
                <Link className={classes.detailed} to={"/Tariffs"}>
                  
                </Link>
              </Typography>
            </Grid>
          </Container>
          <Container
            className={classes.siteTitleContainer}
            id={"siteTitleContainer"}
            maxWidth={false}
          >
            <Box
              className={classes.triangleBottomright}
              style={{
                borderBottomWidth: this.state.triangleBottomWidth + "px"
              }}
            />
            <Container maxWidth={"lg"} className={classes.fullHeight}>
              <Grid
                container
                justify={"center"}
                alignItems={"center"}
                className={classes.fullHeight}
              >
                <Grid
                  container
                  item
                  justify={"flex-start"}
                  className={classes.mainPageTitleContainer}
                  sm={12}
                  md={6}
                  lg={5}
                >
                  <Typography
                    variant={"h4"}
                    color={"textSecondary"}
                    className={classes.siteTitleTextMain}
                  >
                       
                  </Typography>
                  <Typography
                    variant={"h5"}
                    color={"textSecondary"}
                    className={classes.siteTitleTextAdditional}
                  >
                           
                    
                  </Typography>
                  <Link
                    className={classes.detailed}
                    style={{ textDecoration: "none" }}
                    to={"/Functional"}
                  >
                    <Button
                      variant="contained"
                      className={classes.buttonReadMore}
                    >
                         
                    </Button>
                  </Link>
                </Grid>
                <Grid
                  container
                  item
                  justify={"center"}
                  alignItems={"center"}
                  className={classes.fullHeight}
                  sm={7}
                  md={6}
                  lg={7}
                  style={{ zIndex: "1" }}
                >
                  <img
                    src={mainTitleImg}
                    alt={"   "}
                    className={classes.notebookImg}
                  />
                </Grid>
              </Grid>
            </Container>
          </Container>
          <Container
            maxWidth={false}
            className={classes.advantagesMainContainer}
          >
            <Typography
              variant={"h5"}
              color={"textPrimary"}
              className={classes.advantagesBlock}
            >
                
            </Typography>
            <Container maxWidth={"lg"} className={classes.fullHeight}>
              <Grid
                container
                justify={"center"}
                alignItems={"center"}
                className={classes.fullHeight}
              >
                <Grid
                  item
                  container
                  md={3}
                  justify={"flex-start"}
                  alignItems={"center"}
                  direction={"column"}
                  className={classes.advantagesItemContainer}
                >
                  <img src={iconSpeed} alt={"  "} />
                  <Typography
                    variant={"subtitle1"}
                    color={"textPrimary"}
                    className={classes.advantagesMainDescr}
                  >
                    
                  </Typography>
                  <Typography
                    variant={"subtitle2"}
                    color={"textPrimary"}
                    className={classes.advantagesAdditionalDescr}
                  >
                          
                  </Typography>
                </Grid>
                <Grid
                  item
                  container
                  md={3}
                  justify={"flex-start"}
                  alignItems={"center"}
                  direction={"column"}
                  className={classes.advantagesItemContainer}
                >
                  <img
                    src={iconSecurity}
                    alt={"  "}
                  />
                  <Typography
                    variant={"subtitle1"}
                    color={"textPrimary"}
                    className={classes.advantagesMainDescr}
                  >
                    
                  </Typography>
                  <Typography
                    variant={"subtitle2"}
                    color={"textPrimary"}
                    className={classes.advantagesAdditionalDescr}
                  >
                         
                  </Typography>
                </Grid>
                <Grid
                  item
                  container
                  md={3}
                  justify={"flex-start"}
                  alignItems={"center"}
                  direction={"column"}
                  className={classes.advantagesItemContainer}
                >
                  <img src={iconBenefit} alt={"  "} />
                  <Typography
                    variant={"subtitle1"}
                    color={"textPrimary"}
                    className={classes.advantagesMainDescr}
                  >
                    
                  </Typography>
                  <Typography
                    variant={"subtitle2"}
                    color={"textPrimary"}
                    className={classes.advantagesAdditionalDescr}
                  >
                           
                  </Typography>
                </Grid>
                <Grid
                  item
                  container
                  md={3}
                  justify={"flex-start"}
                  alignItems={"center"}
                  direction={"column"}
                  className={classes.advantagesItemContainer}
                >
                  <img
                    src={iconGuarantee}
                    alt={"  "}
                  />
                  <Typography
                    variant={"subtitle1"}
                    color={"textPrimary"}
                    className={classes.advantagesMainDescr}
                  >
                    
                  </Typography>
                  <Typography
                    variant={"subtitle2"}
                    color={"textPrimary"}
                    className={classes.advantagesAdditionalDescr}
                  >
                          
                  </Typography>
                </Grid>
              </Grid>
            </Container>
          </Container>
          <Container
            maxWidth={false}
            className={classes.linkToVideoBlockContainer}
            id={"videoContainer"}
          >
            <canvas
              style={{
                width: this.state.siteTitleContainerSize.width,
                height: this.state.siteTitleContainerSize.height,
                position: "absolute",
                left: "0"
              }}
            />
            <Grid
              container
              direction={"column"}
              justify={"space-around"}
              alignItems={"center"}
              className={classes.linkToVideoTextContainer}
            >
              <Typography variant={"h6"} color={"textSecondary"}>
                   
              </Typography>
              <Button
                variant={"outlined"}
                href={"https://www.youtube.com/watch?v=rTLumvgVKfU"}
                className={classes.buttonLinkToVideo}
                target={"_blank"}
              >
                 
              </Button>
            </Grid>
          </Container>
          <FeedbackForm />
        </Container>
        <Footer />
      </div>
    );
  }
}

LandingPage.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(LandingPage);
