import React from "react";
import Container from "@material-ui/core/Container/Container";
import Header from "components/Header/Header.jsx";
import Footer from "components/Footer/Footer.jsx";
import HeaderLinks from "components/Header/HeaderLinks.jsx";
import { withStyles } from "@material-ui/core";
import FeedbackForm from "../../components/FeedbackForm";
import Button from "@material-ui/core/Button";
import PropTypes from "prop-types";
import Grid from "@material-ui/core/Grid";
import Collapse from "@material-ui/core/Collapse";
import ArrowDown from "@material-ui/icons/KeyboardArrowDown";
import ArrowUp from "@material-ui/icons/KeyboardArrowUp";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import Calculator from "../../components/Calculator";

const styles = () => ({
  featuresMainContainer: {
    padding: "0!important"
  },
  featuresCommonContainer: {
    backgroundColor: "#e3e8ec",
    padding: 0,
    paddingBottom: "40pt"
  },
  featuresMainDescr: {
    fontFamily: "RostelecomBasisMedium!important",
    marginBottom: "10pt!important"
  },
  featuresAdditionalDescr: {
    fontSize: "1rem!important"
  },
  adornmentStyling: {
    fontSize: "0.9rem",
    display: "flex",
    flexDirection: "column",
    marginBottom: "27pt",
    color: "black"
  },
  adornmentStylingAdd: {
    "& > p": {
      fontSize: "1.8rem",
      marginBottom: "2pt",
      marginRight: "29pt",
      color: "black",
      fontFamily: "RostelecomBasisBold!important"
    }
  },
  btnOrder: {
    textTransform: "none",
    fontFamily: "RostelecomBasisRegular",
    width: "100pt",
    height: "45pt",
    fontSize: "1.4rem"
  },
  orderAdditionalDescrContainer: {
    marginTop: "70pt"
  },
  mayOrder: {
    "& p": {
      color: "#0120e3",
      fontSize: "1.2rem",
      marginBottom: 0
    },
    "& ul": {
      paddingLeft: "10pt",
      marginTop: "5pt"
    }
  },
  mayOrderDescr: {
    display: "flex",
    alignItems: "center"
  },
  orderLinks: {
    color: "#0120e3",
    fontFamily: "RostelecomBasisRegular",
    fontWeight: "normal",
    textDecoration: "none",
    cursor: "pointer"
  },
  dialogTextDescription: {
    color: "black",
    fontSize: "1.2rem"
  },
  dialogHeaderText: {
    "& > h2": {
      fontSize: "1.6rem",
      fontFamily: "RostelecomBasisBold"
    }
  }
});

export class Tarifs extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      conditions: false,
      mayRequired: false,
      showDialog: false,
      valueCountLine: ""
    };
  }

  onValueChanged(lineCountCurrentValue) {
    this.setState({
      valueCountLine: lineCountCurrentValue
    });
  }

  handleCollapseCondition() {
    this.setState({
      conditions: !this.state.conditions
    });
  }

  handleCollapseMayRequired() {
    this.setState({
      checked: !this.state.checked
    });
  }

  scrollToBottom() {
    window.scrollTo({
      top: document.body.scrollHeight,
      left: 0,
      behavior: "smooth"
    });
  }

  showDialog() {
    this.setState({
      showDialog: true
    });
  }

  runCloseScenario() {
    this.setState({
      showDialog: false
    });
  }

  render() {
    const { classes } = this.props;

    return (
      <Container
        maxWidth={false}
        className={classes.featuresMainContainer}
        style={{ overflowX: "hidden" }}
      >
        <Dialog
          open={this.state.showDialog}
          onClose={this.runCloseScenario.bind(this)}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle
            id="alert-dialog-title"
            className={classes.dialogHeaderText}
          >
            {": "}
          </DialogTitle>
          <DialogContent>
            <DialogContentText
              id="alert-dialog-description"
              className={classes.dialogTextDescription}
            >
                  31  2019.  
                     
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button
              onClick={this.runCloseScenario.bind(this)}
              color="primary"
              autoFocus
            >
              OK
            </Button>
          </DialogActions>
        </Dialog>
        <Header rightLinks={<HeaderLinks />} fixed />
        <Container maxWidth={false} className={classes.featuresCommonContainer}>
          <Calculator
            fromURI={"test"}
            onValueChanged={this.onValueChanged.bind(this)}
          />
          <Container
            maxWidth={"lg"}
            className={classes.orderAdditionalDescrContainer}
          >
            <Button
              variant="contained"
              color="primary"
              className={classes.btnOrder}
              onClick={this.showDialog.bind(this)}
            >
              
            </Button>
            <Grid item container md={7}>
              <Grid
                item
                container
                md={12}
                direction={"column"}
                className={classes.mayOrder}
              >
                <p
                  onClick={this.handleCollapseCondition.bind(this)}
                  className={classes.mayOrderDescr}
                >
                   {" "}
                  {this.state.conditions ? <ArrowUp /> : <ArrowDown />}
                </p>
                <Collapse in={!this.state.conditions}>
                  <ul>
                    <li>   </li>
                    <li>
                             
                      
                    </li>
                    <li>    </li>
                    <li>
                      {this.state.valueCountLine}    
                        {" "}
                      <a href={"http://roseu.org/roaming/"} target={"_blank"}>
                          
                      </a>
                    </li>
                  </ul>
                </Collapse>
              </Grid>
              <Grid
                item
                container
                md={12}
                direction={"column"}
                className={classes.mayOrder}
              >
                <p
                  onClick={this.handleCollapseMayRequired.bind(this)}
                  className={classes.mayOrderDescr}
                >
                     {" "}
                  {this.state.checked ? <ArrowUp /> : <ArrowDown />}
                </p>
                <Collapse in={!this.state.checked}>
                  <ul>
                    <li>
                        1{" "}
                      <a
                        className={classes.orderLinks}
                        onClick={this.scrollToBottom.bind(this)}
                      >
                        
                      </a>
                    </li>
                    <li>
                        SAP{" "}
                      <a
                        className={classes.orderLinks}
                        onClick={this.scrollToBottom.bind(this)}
                      >
                        
                      </a>
                    </li>
                    <li>
                        Oracle{" "}
                      <a
                        className={classes.orderLinks}
                        onClick={this.scrollToBottom.bind(this)}
                      >
                        
                      </a>
                    </li>
                  </ul>
                </Collapse>
              </Grid>
            </Grid>
          </Container>
        </Container>
        <FeedbackForm />
        <Footer />
      </Container>
    );
  }
}

Tarifs.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(Tarifs);
