import "babel-polyfill";
import React from "react";
import $ from "jquery";
import ReactDOM from "react-dom";
import { HashRouter, Route, Switch } from "react-router-dom";
import indexRoutes from "routes/index.jsx";
import { createMuiTheme } from "@material-ui/core/styles";
import { ThemeProvider } from "@material-ui/styles";
import CssBaseline from "@material-ui/core/CssBaseline";
import BackToTopButton from "components/BackToTopButton/BackToTopButton";
import RostelecomBasisBold from "fonts/woff2/RostelecomBasis-Bold.woff2";
import RostelecomBasisLight from "fonts/woff2/RostelecomBasis-Light.woff2";
import RostelecomBasisMedium from "fonts/woff2/RostelecomBasis-Medium.woff2";
import RostelecomBasisRegular from "fonts/woff2/RostelecomBasis-Regular.woff2";
import RostelecomBasisBoldWoff from "fonts/woff/RostelecomBasis-Bold.woff";
import RostelecomBasisLightWoff from "fonts/woff/RostelecomBasis-Light.woff";
import RostelecomBasisMediumWoff from "fonts/woff/RostelecomBasis-Medium.woff";
import RostelecomBasisRegularWoff from "fonts/woff/RostelecomBasis-Regular.woff";
import { withRouter } from "react-router";

const RostelecomBasis_Bold = {
  fontFamily: "RostelecomBasisBold",
  fontStyle: "bold",
  fontWeight: "bold",
  src: `local('RostelecomBasis-Bold'), url(${RostelecomBasisBold}) format('woff2'), url(${RostelecomBasisBoldWoff}) format('woff')
  `
};
const RostelecomBasis_Regular = {
  fontFamily: "RostelecomBasisRegular",
  fontStyle: "normal",
  fontWeight: "normal",
  src: `local('RostelecomBasis-Regular'), url(${RostelecomBasisRegular}) format('woff2'), url(${RostelecomBasisRegularWoff}) format('woff')
  `
};
const RostelecomBasis_Medium = {
  fontFamily: "RostelecomBasisMedium",
  fontStyle: "normal",
  fontWeight: "normal",
  src: `local('RostelecomBasis-Medium'), url(${RostelecomBasisMedium}) format('woff2'), url(${RostelecomBasisMediumWoff}) format('woff')
  `
};
const RostelecomBasis_Light = {
  fontFamily: "RostelecomBasisLight",
  fontStyle: "light",
  fontWeight: "light",
  src: `local('RostelecomBasis-Light'), url(${RostelecomBasisLight}) format('woff2'), url(${RostelecomBasisLightWoff}) format('woff')
  `
};

const theme = createMuiTheme({
  palette: {
    primary: {
      // light: will be calculated from palette.primary.main,
      main: "#ff4f12"
      // dark: will be calculated from palette.primary.main,
      // contrastText: will be calculated to contrast with palette.primary.main
    }
  },
  overrides: {
    MuiCssBaseline: {
      "@global": {
        "@font-face": [
          RostelecomBasis_Regular,
          RostelecomBasis_Bold,
          RostelecomBasis_Light,
          RostelecomBasis_Medium
        ]
      }
    },
    MuiTypography: {
      colorTextSecondary: {
        color: "#ffffff"
      }
    }
  },
  typography: {
    root: {
      colorTextSecondary: "#ffffff"
    },
    fontFamily: [
      "RostelecomBasisRegular",
      "RostelecomBasisMedium",
      "-apple-system",
      "BlinkMacSystemFont",
      '"Segoe UI"',
      "Roboto",
      '"Helvetica Neue"',
      "Arial",
      "sans-serif",
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"'
    ].join(",")
  }
});

$(document).ready(function() {
  $(document).delegate(".feedbackForm", "submit", function(e) {
    e.preventDefault();
    $.ajax({
      url: "/",
      type: "POST",
      data: {
        message: $(".feedbackForm").serialize(),
        email: "ilyxins@mail.ru",
        subject: "    "
      },
      beforeSend: function() {
        $(".beforeSendNotif").removeClass("sendInfoHidden");
        setTimeout(function() {
          $(".beforeSendNotif").addClass("sendInfoHidden");
        }, 3000);
      },
      success: function() {
        $(".sendSuccessNotif").removeClass("sendInfoHidden");
        setTimeout(function() {
          $(".sendSuccessNotif").addClass("sendInfoHidden");
        }, 3000);
      },
      error: function() {
        $(".sendErrorNotif").removeClass("sendInfoHidden");
        setTimeout(function() {
          $(".sendErrorNotif").addClass("sendInfoHidden");
        }, 3000);
      }
    });
  });
});

ReactDOM.render(
  <ThemeProvider theme={theme}>
    <CssBaseline>
      <HashRouter basename={"/"}>
        <div>
          <Switch>
            {indexRoutes.map((prop, key) => {
              return (
                <Route
                  path={prop.path}
                  key={key}
                  component={withRouter(prop.component)}
                />
              );
            })}
          </Switch>
          <BackToTopButton scrollStepInPx="50" delayInMs="16.66" />
        </div>
      </HashRouter>
    </CssBaseline>
  </ThemeProvider>,
  document.getElementById("root")
);
